import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute} from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import UserAPI from '../../UserAPI'
import Toast from '../../Toast'

class GuideView {
  init(){
    document.title = 'Guide - Rewards Explorer'
    document.body.classList.add('auth-bg')   
    this.render()    
    Utils.pageIntroAnim()
  }

  closeGuide(){
    gotoRoute(`/`)    
  }

  async updateNewUser() {
    try {
      await UserAPI.updateUser(Auth.currentUser._id, {
            newUser: false
      })

      const updatedUser = await UserAPI.getUser(Auth.currentUser._id)
      Auth.currentUser = updatedUser
      localStorage.setItem('user', JSON.stringify(updatedUser))

      gotoRoute('/')
    } catch (err) {
      console.error('Failed to update user:', err)
      Toast.show('Failed to update user preferences', 'error')
    }
  }

  render(){
    const template = html`
      <va-app-header title="Guide" user="${JSON.stringify(Auth.currentUser)}"></va-app-header>
      <div class="page-content">
        <div class="page-inner-content">
          <div class="guide-container">
                <h1>Guide Page</h1>
                <sl-carousel pagination navigation mouse-dragging loop style="--aspect-ratio: 4/3; margin: 2em 0px;">

                  <sl-carousel-item>
                    <img 
                      alt="Slide 1 - Welcome to Reward Explorers, follow these steps to get started."
                      src="https://storage.googleapis.com/reward-explorers-avatars/guide/slide1.jpg">
                  </sl-carousel-item>

                  <sl-carousel-item>
                    <img 
                      alt="Slide 2 - View and update your profile."
                      src="https://storage.googleapis.com/reward-explorers-avatars/guide/slide2.jpg">
                  </sl-carousel-item>

                  <sl-carousel-item>
                    <img 
                      alt="Parents can create and invite family members"
                      src="https://storage.googleapis.com/reward-explorers-avatars/guide/slide3.jpg">
                  </sl-carousel-item>

                  
                  <sl-carousel-item>
                    <img 
                      alt="Kids can manage their tasks by accepting, startig and completing them."
                      src="https://storage.googleapis.com/reward-explorers-avatars/guide/slide4.jpg">
                  </sl-carousel-item>

                  
                  <sl-carousel-item>
                    <img 
                      alt="Parents can also create, view and edit tasks."
                      src="https://storage.googleapis.com/reward-explorers-avatars/guide/slide5.jpg">
                  </sl-carousel-item>

                </sl-carousel>

                <div class="guide-bottom">
                  <sl-button variant="warning" @click=${() => this.updateNewUser() }>Got it! Don't show again.</sl-button>
                  <sl-button variant="primary" @click=${() => this.closeGuide() }>Skip this time!</sl-button>
                </div>
          </div>          
        </div>      

        
      </div>      
    `
    render(template, App.rootEl)
  }
}


export default new GuideView()