import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import FamilyAPI from '../../familyAPI'
import UserAPI from '../../UserAPI'
import Toast from '../../Toast'

class FamilyView {
  async init() {
      document.body.classList.remove('auth-bg') 
      document.title = 'Reward Explorers - My Family'

      try {
      this.family = await FamilyAPI.getFamily(Auth.currentUser.family_id)
      this.render()
      Utils.pageIntroAnim()
      } catch (err) {
      console.error('Error loading family:', err)
      }
  }

  async deleteMember(memberid){
    if (confirm ("Do you wish to delete this family member? This action cannot be undone.")){
      try{
        await UserAPI.deleteMember(memberid)
        Toast.show("User has been deleted")
        this.init()
      } catch (err) {
      console.log( "error deleting" + err)
      Toast.show('Error deleting family member', 'error')
      }
    }else{
      return
    }
  }

  render(){
    const template = html`
        <va-app-header title="Home" user=${JSON.stringify(Auth.currentUser)}></va-app-header>

        <div class="page-content">
          <div class="page-inner-content">
            <h1 class="page-title">${this.family.familyName}</h1>

            <div class="family-container">
              <h3>Family Members</h3>
              <div class="member-container">
                ${this.family?.members.map(
                  member => html`
                    <sl-card>
                    <div class="member-card-top">
                       <sl-avatar 
                        style="--size: 200px; margin-bottom: 1em;" 
                        image=${member.avatarUrl || ''}>
                      </sl-avatar><br>
                      <h4>${member.firstName} ${member.lastName}</h4>
                      <p>${member.email}</p>
                    </div>

                      <div slot="footer">
                      ${Auth.currentUser.accessLevel === 1
                        ? html`<sl-button variant="danger" outline @click=${() => this.deleteMember(member._id)}>Delete</button>`
                        : ''}

                        <sl-button variant="success" @click=${() => gotoRoute(`/profile/${member._id}`)}>View Profile</sl-button>
                      </div>
                    </sl-card>
                  `
                )}
            </div>

            <div class="family-container-bottom">
                <sl-button variant="neutral" @click="${() => gotoRoute("/")}">Return Home</sl-button>

                              ${Auth.currentUser.accessLevel === 1
              ? html `<sl-button variant="primary" @click="${() => gotoRoute("/addMember")}">Add new family member</sl-button>
              ` : ''}
            </div>

            </div>
          </div> 
          <img class="background-bird bird-btm-right bird-flipped small-bird" src="/images/bird4.svg">
          <img class="background-bird bird-top-left small-bird" src="/images/bird1.svg">
        </div>      
    `
    render(template, App.rootEl)
  }
}

export default new FamilyView()