import App from './../../App'
import Auth from './../../Auth'
import {html, render } from 'lit-html'
import {anchorRoute, gotoRoute} from './../../Router'
import Utils from './../../Utils'

class SignUpView{   
  init(){      
    document.title = 'Sign Up - Rewards Explorer'   
    document.body.classList.add('auth-bg') 
    this.render()
    Utils.pageIntroAnim()
  }

  signUpSubmitHandler(e){
    e.preventDefault()
    const form = e.target
    const formData = new FormData(form)

    const familyName = formData.get('familyName').trim()
    const familySuffix = formData.get('familySuffix').trim()
    const combinedFamilyName = familyName + ' ' + familySuffix
    formData.set('familyName', combinedFamilyName)
    formData.delete('familySuffix')

    const submitBtn = document.querySelector('.submit-btn')
    submitBtn.setAttribute('loading', '')    
    
    // sign up using Auth
    Auth.signUp(formData, () => {
      submitBtn.removeAttribute('loading')
    })   
  }

  render(){
    const familySuffixes = [
      'Family', 'Clan', 'Tribe', 'Pack', 'Crew', 'Team', 'House', 
      'Household', 'Circle', 'Lineage', 'Collective', 'Unit', 'Group', 
      'Gang', 'Posse', 'Band', 'Order', 'Syndicate', 'Fellowship', 'Troop', 'Brotherhood', 'Sisterhood'
    ]

    const template = html`      
      <div class="page-content page-centered">      
        <div class="signinup-box">
        <img class="signinup-logo" src="./images/logo.svg">
          <h1>Sign Up</h1>
          <form class="form-signup" @submit=${this.signUpSubmitHandler}>
            <div class="input-group">
              <sl-input name="firstName" type="text" placeholder="First Name" autocomplete="off" required></sl-input>
            </div>
            <div class="input-group">
              <sl-input name="lastName" type="text" placeholder="Last Name" autocomplete="off" required></sl-input>
            </div>
            <div class="input-group">
              <sl-input name="email" type="email" placeholder="Email" autocomplete="off" required></sl-input>
            </div>
            <div class="input-group">
              <sl-input name="password" type="password" placeholder="Password" autocomplete="off" required password-toggle></sl-input>
            </div>
            <div class="orange-input">
              <h4>Create your family!</h4>
              <div class="input-group" class"familyinput">
                <sl-input name="familyName"  type="text" placeholder="Family Name" autocomplete="off" required></sl-input>
              </div>
              <div class="input-group">
                <sl-select name="familySuffix" value="Family" required>
                  ${familySuffixes.map(
                    suffix => html`<sl-option value=${suffix}>${suffix}</sl-option>`
                  )}
                </sl-select>
              </div>
            </div>

                    
            <sl-button variant="primary" type="submit" class="submit-btn" style="width: 100%;">Sign Up</sl-button>
          </form>
          <p>Have an account? <a href="/signin" @click=${anchorRoute}>Sign In</a></p>
        </div>
      </div>
    `
    render(template, App.rootEl)
  }  
}


export default new SignUpView()