import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from '../../Auth'

class FourOFourView{
  init(){
    console.log('FourOFourView.init')    
    document.title = '404 File not found'    
    this.render()
  }

  render(){
    const template = html`        
      <div class="calign">
        <h1>404!</h1>
        <p>Sorry, we couldn't find that.</p>
        <sl-button variant="warning" @click="${() => gotoRoute("/")}">Return Home</sl-button>
      </div>
      <img class="background-bird bird-btm-left big-bird" src="/images/bird2.svg">
    `
    render(template, App.rootEl)
  }
}

export default new FourOFourView()