import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import FamilyAPI from '../../familyAPI'
import TaskAPI from '../../taskAPI'
import moment from 'moment'


class HomeView {
  async init(){    
    document.title = 'Home - Reward Explorers'  
    document.body.classList.remove('auth-bg') 
    this.family = await FamilyAPI.getFamily(Auth.currentUser.family_id)
    this.tasks = await TaskAPI.getTasks(Auth.currentUser.family_id)  

    this.render()    
    Utils.pageIntroAnim()
  }



  render(){
    const currentUserId = String(Auth.currentUser._id)
    const isAdmin = Auth.currentUser.accessLevel === 1
    const inProgressTasks = this.tasks.filter(t =>
      t.status === 'In_Progress' &&
      (isAdmin || (t.owner_id && String(t.owner_id._id || t.owner_id) === currentUserId))
    )

    const completedTasks = this.tasks
      .filter(t =>
        t.status === 'Completed' &&
        t.completedDate &&
        (isAdmin || (t.owner_id && String(t.owner_id._id || t.owner_id) === currentUserId))
      )
      .sort((a, b) => new Date(b.completedDate) - new Date(a.completedDate))
      .slice(0, 5)

    const assignedTasks = this.tasks.filter(t =>
      t.status === 'Assigned' &&
      t.owner_id &&
      String(t.owner_id._id || t.owner_id) === currentUserId
    )

        const createdTasks = this.tasks.filter(t =>
      isAdmin || (t.creator_id && String(t.creator_id._id || t.creator_id) === currentUserId)
    )

    const visibleCompletedTasks = completedTasks.filter(t =>
      isAdmin || (t.owner_id && String(t.owner_id._id || t.owner_id) === currentUserId)
    )

    const visibleAssignedTasks = this.tasks.filter(t =>
      t.status === 'Assigned' &&
      (isAdmin || (t.owner_id && String(t.owner_id._id || t.owner_id) === currentUserId))
    )

    const visibleInProgressTasks = inProgressTasks.filter(t =>
      isAdmin || (t.owner_id && String(t.owner_id._id || t.owner_id) === currentUserId)
    )



    const template = html`
      <va-app-header title="Home" user=${JSON.stringify(Auth.currentUser)}></va-app-header>
      
      <div class="page-content">

      <div class="page-inner-content">

        <h1 class="anim-in page-title">Welcome back ${Auth.currentUser.firstName}!</h1>

        <div class="home-grid">


          <div class="home-section family">
            <h2><sl-icon name="house"></sl-icon>${this.family.familyName}</h2>
            <sl-divider></sl-divider>

            <div class="section-middle">
              <h3>Members</h3>

              <div class="member-list">
                ${this.family?.members.map(
                  member => html`
                    <sl-tooltip content="${member.firstName } ${member.lastName }">
                        <sl-avatar 
                          label="${member.firstName } ${member.lastName }"
                          image=${member.avatarUrl || ''}
                        ></sl-avatar>                
                    </sl-tooltip>
                  `
                )}
              </div>
            </div>
            <div class="section-bottom">          
                <sl-animation name="pulse" easing="ease-in-out" duration="2000" play> 
                          <sl-button variant="success" outline  @click=${() => gotoRoute('/family')}>View Family</sl-button>
                </sl-animation>
            </div>
          </div>


          <div class="home-section task-roundup">
            <h2><sl-icon name="calendar-week"></sl-icon>Task Roundup</h2>
            <sl-divider></sl-divider>

            <div class="section-middle stats">
              ${isAdmin
                ? html`<p><strong>Created:</strong> ${createdTasks.length}</p>`
                : ''}
              <p><strong>Completed:</strong> ${visibleCompletedTasks.length}</p>
              <p><strong>Assigned:</strong> ${visibleAssignedTasks.length}</p>
              <p><strong>In Progress:</strong> ${visibleInProgressTasks.length}</p>
            </div>

            <div class="section-bottom">
            <sl-animation name="pulse" easing="ease-in-out" duration="2000" play> 
              <sl-button variant="warning" outline  @click=${() => gotoRoute('/tasks')}>View Tasks</sl-button>  
              </sl-animation>   
            </div>

          </div>

          <div class="home-section task">
            <h2><sl-icon name="hourglass-split"></sl-icon>In Progress</h2>
            <sl-divider></sl-divider>
            <div class="tasks-list">
            ${inProgressTasks.map(task => html`
              <re-task-card .task=${task}></re-task-card>
            `)}
            </div>
          </div>         

          <div class="home-section task"> 
            ${Auth.currentUser.accessLevel === 1
              ? html`
                  <h2><sl-icon name="check2-square"></sl-icon>Recently Completed</h2>
                  <sl-divider></sl-divider>
                  <div class="tasks-list">
                  ${completedTasks.map(task => html`
                    <re-task-card .task=${task}></re-task-card>
                  </div>
                  `)}
                `
              : html`
                  <h2><sl-icon name="clipboard-plus"></sl-icon>Assigned Tasks</h2>
                  <sl-divider></sl-divider>
                  <div class="tasks-list">
                  ${assignedTasks.map(task => html`
                    <re-task-card .task=${task}></re-task-card>
                    </div>
                  `)}
                `}
          </div>
        </div> 
        
      </div>
      <img class="background-bird bird-btm-left small-bird" src="/images/bird1.svg">
      <img class="background-bird bird-top-right bird-flipped small-bird" src="/images/bird4.svg">
    </div>     
    `
    render(template, App.rootEl)

    App.rootEl.querySelectorAll('re-task-card').forEach(card => {
        card.addEventListener('task-updated', async () => {
            this.tasks = await TaskAPI.getTasks(Auth.currentUser.family_id)
            this.render()
        })
        })
  }
}

export default new HomeView()