import App from './App'
import Auth from './Auth'
import Toast from './Toast'

class TaskAPI {
    // Getting tasks by family id
    async getTasks(familyId) {
        // validate family Id
        if (!familyId) return

        // Requesting tasks by family id
        try {
            const response = await fetch(`${App.apiBase}/task/family/${familyId}`, {
            headers: {
                'Content-Type': 'application/json'
            }
            })

            if (!response.ok) throw new Error(`Failed to fetch tasks: ${response.status}`)

            const tasks = await response.json()
            return tasks
        } catch (err) {
            console.error('Error fetching tasks:', err)
            throw err
        }
    }

    // Getting tasks by task id
    async getTask(id) {
        const res = await fetch(`${App.apiBase}/task/${id}`, {
            headers: {
            Authorization: `Bearer ${localStorage.accessToken}`
            }
        })
        return res.json()
    }

    // Updating task by id with new data
    async updateTask(id, data) {
        const res = await fetch(`${App.apiBase}/task/${id}`, {
            method: 'PUT',
            headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.accessToken}`
            },
            body: JSON.stringify(data)
        })
        console.log(res)
        if (!res.ok) throw new Error('Task update failed')
        return res.json()
    }
}

export default new TaskAPI()