import App from './../../App'
import { html, render } from 'lit-html'
import { gotoRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import UserAPI from '../../UserAPI'
import Toast from '../../Toast'

class ProfileView {
  async init() {
    document.body.classList.remove('auth-bg') 
    document.title = 'My Profile - Rewards Explorer'

    this.sameUser = false
    this.profileUserId = window.location.pathname.split('/').pop()

    try {
      this.profileUser = await UserAPI.getUser(this.profileUserId)

      if (
        this.profileUser._id === Auth.currentUser._id ||
        this.profileUser._id === Auth.currentUser.id
      ) {
        this.sameUser = true
      }

      this.render()
      Utils.pageIntroAnim()
    } catch (err) {
      console.error('Failed to load user profile:', err)
    }
  }

  updateCurrentUserContext(updatedUser) {
    const existingUser = Auth.currentUser || {}
    const mergedUser = { ...existingUser, ...updatedUser }

    localStorage.setItem('user', JSON.stringify(mergedUser))    
    Auth.currentUser = mergedUser
    this.profileUser = mergedUser
    document.dispatchEvent(new CustomEvent('user-updated'))
  }

  isOwnProfile() {
    return this.sameUser
  }

  showAvatarDialog() {
    const dialog = document.getElementById('avatarDialog')
    if (dialog) dialog.show()
  }

  closeDialog() {
    const dialog = document.getElementById('avatarDialog')
    if (dialog) dialog.hide()
  }

  handleAvatarUpload = async (event) => {
    const file = event.target.files[0]
    if (!file) return

    if (!this.isOwnProfile()) {
      Toast.show('You can only update your own profile picture', 'error')
      return
    }

    const formData = new FormData()
    formData.append('avatar', file)

    try {
      const updatedUser = await UserAPI.uploadAvatar(this.profileUser._id, formData)

      this.updateCurrentUserContext(updatedUser)
      Toast.show('Avatar uploaded!')
      this.closeDialog()

      event.target.value = '' // allow re-upload of same file
      this.render()
    } catch (err) {
      console.error(err)
      Toast.show('Upload failed', 'error')
    }
  }

  updateProfileHandler = async (e) => {
    e.preventDefault()

    if (!this.isOwnProfile()) {
      Toast.show('You can only update your own profile', 'error')
      return
    }

    const form = e.target
    const formData = new FormData(form)
    const updatedData = {
      firstName: formData.get('firstName').trim(),
      lastName: formData.get('lastName').trim(),
      email: formData.get('email').trim()
    }

    const password = formData.get('password')
    if (password && password.trim().length > 0) {
      updatedData.password = password.trim()
    }

    try {
      const updatedUser = await UserAPI.updateUser(this.profileUser._id, updatedData)

      this.updateCurrentUserContext(updatedUser)
      Toast.show('Profile updated successfully!')

      const passwordField = form.querySelector('sl-input[name="password"]')
      if (passwordField) passwordField.value = ''

      this.render()
    } catch (err) {
      console.error(err)
      Toast.show('Update failed', 'error')
    }
  }

  render() {
    const template = html`
      <va-app-header .title="Profile" .user=${Auth.currentUser}></va-app-header>
      <div class="page-content">
        <div class="page-inner-content">
          <h1 class="page-title">${this.sameUser ? 'My Profile' : 'View Profile'}</h1>

          <div class="profile-container">
            <div class="profile-container-upper">
              <div>
                <h2>${this.profileUser.firstName} ${this.profileUser.lastName}</h2>
                <p>${this.profileUser.email}</p>
              </div>

              <div class="column-align">
                ${this.profileUser?.avatarUrl
                  ? html`
                      <sl-avatar
                        style="--size: 200px; margin-bottom: 1em;"
                        image=${this.profileUser.avatarUrl}
                      ></sl-avatar>
                    `
                  : html`
                      <sl-avatar style="--size: 200px; margin-bottom: 1em;"></sl-avatar>
                    `}
                ${this.sameUser
                  ? html`
                      <sl-button
                        size="small"
                        variant="success"
                        outline
                        @click=${() => this.showAvatarDialog()}
                      >
                        Change profile picture
                      </sl-button>
                    `
                  : ''}
              </div>
            </div>

            ${this.sameUser
              ? html`
                  <sl-details summary="Edit Profile" class="profile-details">
                    <form @submit=${this.updateProfileHandler}>
                      <sl-input
                        name="firstName"
                        label="First Name"
                        value=${this.profileUser.firstName || ''}
                      ></sl-input>
                      <sl-input
                        name="lastName"
                        label="Last Name"
                        value=${this.profileUser.lastName || ''}
                      ></sl-input>
                      <sl-input
                        name="email"
                        label="Email"
                        value=${this.profileUser.email || ''}
                      ></sl-input>
                      <sl-input
                        type="password"
                        name="password"
                        label="Password"
                        placeholder="Enter new password"
                      ></sl-input>

                      <sl-button type="submit" class="update-btn" variant="success"
                        >Update</sl-button
                      >
                    </form>
                  </sl-details>
                `
              : ''}

            <sl-button outline variant="neutral" @click=${() => gotoRoute('/')}>
              Return Home
            </sl-button>
          </div>
        </div>
        <img class="background-bird bird-btm-left big-bird" src="/images/bird2.svg">
      </div>

      ${this.sameUser
        ? html`
            <sl-dialog
              id="avatarDialog"
              label="Update Profile Picture"
              class="avatar-dialog"
            >
              <form id="avatarForm">
                <input
                  type="file"
                  name="avatar"
                  accept="image/*"
                  @change=${this.handleAvatarUpload}
                />
              </form>
            </sl-dialog>
          `
        : ''}
    `
    render(template, App.rootEl)
  }
}

export default new ProfileView()
