import App from './App'
import Auth from './Auth'
import Toast from './Toast'


class UserAPI {  
  async updateUser(userId, userData){
    // validate
    if(!userId || !userData) return

    console.log('UserAPI.updateUser called with:', userId, userData)
    
    // make fetch request to backend
    const response = await fetch(`${App.apiBase}/user/${userId}`, {
      method: "PUT",
      headers: {
        "Authorization": `Bearer ${localStorage.accessToken}`,
        "Content-Type": "application/json"
      },
        body: JSON.stringify(userData) 
    })

    // if response not ok
    if(!response.ok){
      // console log error
      const err = await response.json()
      if(err) console.log(err)
      // throw error (exit this function)      
      throw new Error('Problem updating user')
    }

    // convert response payload into json - store as data
    const data = await response.json()
    
    // return data
    return data
  }

  async getUser(userId){
    // validate
    if(!userId) return
    
    // fetch the json data
    const response = await fetch(`${App.apiBase}/user/${userId}`, {
      headers: { "Authorization": `Bearer ${localStorage.accessToken}`}
    })

    // if response not ok
    if(!response.ok){ 
      // console log error
      const err = await response.json()
      if(err) console.log(err)
      // throw error (exit this function)      
      throw new Error('Problem getting user')
    }
    
    // convert response payload into json - store as data
    const data = await response.json()
    
    // return data
    return data
  }

  // Uploading new user avatar
  async uploadAvatar(userId, formData) {
    const res = await fetch(`${App.apiBase}/user/${userId}/avatar`, {
      method: 'PUT',
      headers: {
        Authorization: `Bearer ${localStorage.accessToken}`
      },
      body: formData
    });
    if (!res.ok) throw new Error('Avatar upload failed');
    return await res.json();
  }


  // Deleting member using delete call
  async deleteMember(userId) {
    const res = await fetch(`${App.apiBase}/user/${userId}`,{
      method: 'DELETE',
      headers: {
        Authorization: `Bearer ${localStorage.accessToken}`
      }
    })
    if (!res.ok) throw new Error('Delete family member failed');
    return await res.json();
  }

}
export default new UserAPI()