import { LitElement, html, css } from '@polymer/lit-element'
import { gotoRoute } from '../Router'
import Auth from '../Auth'
import moment from 'moment'
import TaskAPI from '../taskAPI'
import Toast from '../Toast'

customElements.define('re-task-card', class TaskCard extends LitElement {
  constructor() {
    super()
  }

  static get properties() {
    return {
      task: { type: Object }
    }
  }

  async acceptTask() {
    try {
      const updatedTask = {
        owner_id: Auth.currentUser._id,
        status: 'Assigned'
      }
      await TaskAPI.updateTask(this.task._id, updatedTask)
      Toast.show('Task accepted')
      this.closeDialog()
      this.dispatchEvent(new CustomEvent('task-updated', { bubbles: true, composed: true }))
    } catch (err) {
      console.error('Error accepting task:', err)
    }
  }

  async startTask() {
    try {
      const updatedTask = {
        status: 'In_Progress',
        inProgressDate: Date.now()
      }
      await TaskAPI.updateTask(this.task._id, updatedTask)
      Toast.show('Task started')
      this.closeDialog()
      this.dispatchEvent(new CustomEvent('task-updated', { bubbles: true, composed: true }))
    } catch (err) {
      console.error('Error starting task:', err)
    }
  }

  async completeTask() {
    try {
      const updatedTask = {
        status: 'Completed',
        completedDate: Date.now()
      }
      await TaskAPI.updateTask(this.task._id, updatedTask)
      Toast.show('Task completed')
      this.closeDialog()
      this.dispatchEvent(new CustomEvent('task-updated', { bubbles: true, composed: true }))
    } catch (err) {
      console.error('Error completing task:', err)
    }
  }

  render() {
    if (!this.task) {
      return html`<sl-card>No task data available</sl-card>`
    }

    return html
    `
    <style>
      sl-card{
        width: 300px;
    

        h2 {
          font-family: Yomogi;
          padding: 0;
          margin: 0;
          font-size: 1.3em;
        }

        p {
        margin: 0 0 1em 0;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;  
          overflow: hidden;      
        }     
      }
      
      .card-head{
       display: flex;
       justify-content: space-between;
       align-items: center;
        }

      .avatar-group sl-avatar:not(:first-of-type) {
        margin-left: -1rem;
      }

      .avatar-group sl-avatar::part(base) {
        border: solid 2px var(--sl-color-neutral-0);
      }

      .card-content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        gap: 0.5em;
      }
      .reward {
      font-size: small;
        color: var(--sl-color-success-600);
      }
      .created-date {
        font-size: 0.875rem;
        color: var(--sl-color-neutral-500);
      }
      .footer-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
      }

      .dialog-top {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;      
      }
    </style>

    

    <sl-card>
      <div slot="header" >

        <div class="card-head">
          <h2>${this.task.taskTitle}</h2>
          <sl-button size="small" variant="neutral" @click=${this.openDialog}>View</sl-button>
        </div>

      </div>
        
      <div class="card-content">

        <div>
          <p>${this.task.description}</p>
          Reward: ${this.task.reward}
        </div>

        <div class="created-date">
          Created: ${this.task.createdDate ? moment(this.task.createdDate).format('DD-MM-YY @ hh:MM a') : 'No date'}
        </div>
          
      </div>

      <div slot="footer">
        <div class="footer-content">
          <div class="avatar-group">
            <sl-avatar
            image=${this.task.creator_id?.avatarUrl ?? ''}
            >
            ${this.task.creator_id
                ? (this.task.creator_id.firstName?.charAt(0))
                : 'C'}
            </sl-avatar>


            ${this.task.owner_id ? html`

            <sl-avatar
              image=${this.task.owner_id.avatarUrl ?? ''}
            >
              ${this.task.owner_id.firstName?.charAt(0)}
            </sl-avatar>
                ` : ''}

            </div>

            <sl-badge variant="${this.getStatusVariant()}">
              ${this.task.status || 'Unknown'}
            </sl-badge>

          </div>
      </div>
    </sl-card>

    <sl-dialog label="${this.task.taskTitle}" id="task-dialog">

      <div class="dialog-top">
            <div class="avatar-group">
              <sl-tooltip content="${this.task.creator_id.firstName} ${this.task.creator_id.lastName}">
                <sl-avatar
                  image=${this.task.creator_id?.avatarUrl ?? ''}
                  >
                </sl-avatar>
              </sl-tooltip>

              ${this.task.owner_id ? html`
              <sl-tooltip content="${this.task.owner_id.firstName} ${this.task.owner_id.lastName}">
                <sl-avatar
                  image=${this.task.owner_id.avatarUrl ?? ''}
                >
                </sl-avatar>
              </sl-tooltip>
               ` : ''}
            </div>

            <sl-badge variant="${this.getStatusVariant()}">
              ${this.task.status || 'Unknown'}
            </sl-badge>

      </div>

      <p>${this.task.description}</p>
      <p>Reward: ${this.task.reward}</p>
      <p>
        Created Date:     ${this.task.createdDate ? moment(this.task.createdDate).format('DD-MM-YY @ hh:MM a') : 'No date'} <br>
        Start Date:       ${this.task.inProgressDate ? moment(this.task.inProgressDate).format('DD-MM-YY @ hh:MM a') : 'Not started'}<br>
        Completed Date:   ${this.task.completedDate ? moment(this.task.inProgressDate).format('DD-MM-YY @ hh:MM a') : 'Not completed'}
      </p>

        
      ${Auth.currentUser.accessLevel === 2 && this.task.status === 'Pending' && !this.task.owner_id
      ? html`
          <sl-button variant="success" @click=${() => this.acceptTask()}>Accept</sl-button>
        `
      : ''}

      ${Auth.currentUser.accessLevel === 2 && this.task.status === 'Assigned' &&
        String(Auth.currentUser._id) === String(this.task.owner_id?._id || this.task.owner_id)
        ? html`
            <sl-button variant="warning" @click=${() => this.startTask()}>Start</sl-button>
          `
        : ''}

      ${Auth.currentUser.accessLevel === 2 && this.task.status === 'In_Progress' &&
        String(Auth.currentUser._id) === String(this.task.owner_id?._id || this.task.owner_id)
        ? html`
            <sl-button variant="primary" @click=${() => this.completeTask()}>Complete</sl-button>
          `
        : ''}

      ${Auth.currentUser.accessLevel === 1
        ? html`
            <sl-button variant="warning" @click=${() => gotoRoute(`/task/${this.task._id}`)}>
              Edit Task
            </sl-button>
          `
        : ''}

    </sl-dialog>
    `
  }

  openDialog() {
    this.shadowRoot.getElementById('task-dialog').show()
  }

  closeDialog() {
    const dialog = this.shadowRoot.getElementById('task-dialog')
    if (dialog) {
      dialog.hide()
    }
  }

  getStatusVariant() {
    if (!this.task.status) return 'neutral';

    switch (this.task.status) {
        case 'Completed':
        return 'success';
        case 'In_Progress':
          return 'primary';
        case 'Assigned':
        return 'warning';
        case 'Pending':
        return 'neutral';
        default:
        return 'neutral';
    }
  }

  
})